import { Component, OnInit } from '@angular/core'; import { Router } from '@angular/router';  @Component({   moduleId: module.id,   selector: 'sistema-dados',   templateUrl: 'sistema_dados.component.html' }) export class SistemaDadosComponent implements OnInit {       displayMode: SistemaDadosDisplayModeEnum;     displayModeEnum = SistemaDadosDisplayModeEnum;       constructor(private router: Router) { }      ngOnInit() {       const path = this.router.url.split('/')[3];       switch (path) {         case 'details':           this.displayMode = SistemaDadosDisplayModeEnum.Details;           break;         case 'info':           this.displayMode = SistemaDadosDisplayModeEnum.Orders;           break;         case 'edit':           this.displayMode = SistemaDadosDisplayModeEnum.Edit;           break;       }     }  }  enum SistemaDadosDisplayModeEnum {   Details=0,   Orders=1,   Edit=2 }


